import React, { Component } from "react";
import axios from "axios";
import {
	Card,
	Table,
	Tag,
	Tooltip,
	message,
	Button,
	Modal,
	Layout,
	Typography,
	Radio,
	Input,
	Row,
	Col,
	Image,
	Space,
} from "antd";
import {
	EyeOutlined,
	DeleteOutlined,
	CheckOutlined,
	CloseOutlined,
} from "@ant-design/icons";
import {
	CompressOutlined,
	UserOutlined,
	CalendarOutlined,
} from "@ant-design/icons";
import dayjs from "dayjs";
import UserView from "./UserView";
import AvatarStatus from "components/shared-components/AvatarStatus";
import userData from "assets/data/user-list.data.json";

const { Header, Footer, Sider, Content } = Layout;

const { TextArea } = Input;

export class PersonalInfo extends Component {
	state = {
		users: [],
		userProfileVisible: false,
		selectedUser: null,
		isModalOpen: false,
		radioValue: "",
		rejectReasonValue: "",
	};

	componentDidMount() {
		axios.get("http://localhost:5000/api/management/allUsers").then((res) => {
			this.setState({
				users: res.data.allUser,
			});
		});
	}

	handleStatus = (user, newStatus) => {
		this.setState((prevState) => ({
			users: prevState.users.map((item) => {
				if (item._id === user._id) {
					return {
						...item,
						emailStatus: newStatus,
						emailStatusComment: this.state.rejectReasonValue,
					};
				}
				return item;
			}),
		}));
	};

	showModal = (user) => {
		this.setState({
			isModalOpen: true,
			selectedUser: user,
			radioValue: user.emailStatus,
			rejectReasonValue: user.emailStatusComment ?? "",
		});
	};

	handleOk = () => {
		this.setState({
			isModalOpen: false,
		});
		axios
			.post("http://localhost:5000/api/management/changeInfoStatus", {
				...this.state.selectedUser,
				emailStatus: this.state.radioValue,
				emailStatusComment: this.state.rejectReasonValue,
			})
			.then((res) => {
				this.handleStatus(this.state.selectedUser, this.state.radioValue);
				console.log(res.data);
			})
			.catch((err) => {
				console.log(err);
			});
	};

	handleCancel = () => {
		this.setState({ isModalOpen: false });
	};

	handleStatusChange = (index, newStatus) => {
		const updatedLists = [...this.state.lists];
		updatedLists[index] = newStatus;
		this.setState({ lists: updatedLists });
		console.log(this.state.lists);
	};

	onChangeRadio = (e) => {
		this.setState({
			radioValue: e.target.value,
		});
		this.handleStatus(this.state.selectedUser, this.state.radioValue);
	};

	onChangeRejectReason = (e) => {
		this.setState({
			rejectReasonValue: e.target.value,
		});
	};

	render() {
		const { users, userProfileVisible, selectedUser } = this.state;
		const defaultAvatar = "/img/avatars/default.png";

		const tableColumns = [
			{
				title: "User",
				dataIndex: "name",
				render: (_, record) => (
					<div className="d-flex">
						<AvatarStatus
							src={record.avatar !== "" ? record.avatar : defaultAvatar}
							name={record.firstname + " " + record.lastname}
							subTitle={record.email}
						/>
					</div>
				),
				sorter: {
					compare: (a, b) => {
						a = a.firstname.toLowerCase();
						b = b.firstname.toLowerCase();
						return a > b ? -1 : b > a ? 1 : 0;
					},
				},
			},
			{
				title: "Role",
				dataIndex: "role",
				sorter: {
					compare: (a, b) => a.role.length - b.role.length,
				},
			},
			{
				title: "Last online",
				dataIndex: "lastOnline",
				render: (date) => <span>{dayjs.unix(date).format("MM/DD/YYYY")} </span>,
				sorter: (a, b) =>
					dayjs(a.lastOnline).unix() - dayjs(b.lastOnline).unix(),
			},
			{
				title: "Status",
				dataIndex: "status",
				render: (_, elm) =>
					elm.emailStatus === false ? (
						<Tooltip title={elm.emailStatusComment}>
							<Tag className="text-capitalize" color={"red"}>
								rejected
							</Tag>
						</Tooltip>
					) : (
						<Tag
							className="text-capitalize"
							color={elm.emailStatus === true ? "cyan" : "orange"}
						>
							{elm.emailStatus === true ? "approved" : "pending"}
						</Tag>
					),
				sorter: {
					compare: (a, b) => a.emailStatus.length - b.emailStatus.length,
				},
			},
			{
				title: "Actions",
				dataIndex: "actions",
				render: (_, elm) => (
					<div className="text-right d-flex justify-content-end">
						{/* <Tooltip title="approve">
              <Button
                className="mr-2"
                icon={<CheckOutlined />}
                onClick={() => {
                  this.handleStatus(elm, "active");
                }}
                disabled={elm.status === "active"}
              />
            </Tooltip>
            <Tooltip title="block">
              <Button
                className="mr-2"
                icon={<CloseOutlined />}
                onClick={() => {
                  this.handleStatus(elm, "blocked");
                }}
                disabled={elm.status === "blocked"}
              />
            </Tooltip> */}
						<Tooltip title="View">
							<Button
								type="primary"
								className="mr-2"
								icon={<EyeOutlined />}
								onClick={() => this.showModal(elm)}
								size="small"
							/>
						</Tooltip>
						{/* <Tooltip title="Delete">
							<Button
								danger
								icon={<DeleteOutlined />}
								onClick={() => {
									this.deleteUser(elm.id);
								}}
								size="small"
							/>
						</Tooltip> */}
					</div>
				),
			},
		];
		return (
			<Card bodyStyle={{ padding: "0px" }}>
				<div className="table-responsive">
					<Table columns={tableColumns} dataSource={users} rowKey="id" />
				</div>
				<Modal
					open={this.state.isModalOpen}
					onOk={this.handleOk}
					onCancel={this.handleCancel}
					// okButtonProps={{ disabled: true }}
					okButtonProps={{
						disabled:
							this.state.radioValue === false &&
							this.state.rejectReasonValue === "",
					}}
					centered
					width="40%"
				>
					<Layout>
						<Header style={{ padding: "0 20px" }}>
							<p style={{ fontSize: "28px", marginBottom: "0px" }}>
								User Information
							</p>
						</Header>
						<Content
							style={{
								padding: "0 50px",
								background: "none",
							}}
						>
							<Typography style={{ marginBottom: "10px" }}>
								<UserOutlined style={{ padding: "0 10px" }} />
								<Typography.Text style={{ fontSize: "18px" }}>
									Name:{" "}
									{this.state.selectedUser?.firstname +
										" " +
										this.state.selectedUser?.lastname}
								</Typography.Text>
							</Typography>
							<Typography style={{ marginBottom: "10px" }}>
								<CalendarOutlined style={{ padding: "0 10px" }} />
								<Typography.Text style={{ fontSize: "18px" }}>
									Birthday: {this.state.selectedUser?.birthday}
								</Typography.Text>
							</Typography>
							<Typography style={{ marginBottom: "10px" }}>
								<CompressOutlined style={{ padding: "0 10px" }} />
								<Typography.Text style={{ fontSize: "18px" }}>
									Location: {this.state.selectedUser?.country}
								</Typography.Text>
							</Typography>
							<Typography style={{ margin: "20px" }}>
								<Radio.Group
									onChange={this.onChangeRadio}
									value={this.state.radioValue}
								>
									<Radio value={true}>Approve</Radio>
									<Radio value={false}>Reject</Radio>
								</Radio.Group>
							</Typography>
							{this.state.radioValue === false ? (
								<TextArea
									value={this.state.rejectReasonValue}
									onChange={this.onChangeRejectReason}
									placeholder="Please enter the reject reason."
									autoSize={{ minRows: 3, maxRows: 5 }}
								/>
							) : null}
						</Content>
					</Layout>
				</Modal>
			</Card>
		);
	}
}

export default PersonalInfo;
